.diagonals {
  position: relative;
  background-color: orange;
  overflow: hidden;
  height: 100vh;
  --width: 100vw;
  --full-width: 100vw;
  --angle: -45deg;
  /*-----------------
  Magic Number Alert:

  the 0.09719 is calculated by this formula:
  tan(45°) / 2
  11° is the current skewY value.
  ------------------*/
  --magic-number: 0.05;
  --skew-padding: calc(var(--width) * var(--magic-number));
  --clip-padding: calc(var(--full-width) * var(--magic-number)); }
  .diagonals .diagonal__wrapper {
    margin-top: -30%; }
    .diagonals .diagonal__wrapper.active .diagonal-box {
      height: 5%;
      padding: 20px 0; }
      .diagonals .diagonal__wrapper.active .diagonal-box .diagonal-bg-text {
        display: block;
        top: -55px;
        font-size: 35px; }
      .diagonals .diagonal__wrapper.active .diagonal-box:not(.active) .diagonal-bg-text {
        transform: rotate(-45deg) translateY(-30px); }
      .diagonals .diagonal__wrapper.active .diagonal-box:not(.active) .diagonal-box__title {
        display: none; }
      .diagonals .diagonal__wrapper.active .diagonal-box:not(.active).bg-1 .diagonal-bg-text {
        transform: rotate(-45deg) translateY(-5px) translateX(50px); }
      .diagonals .diagonal__wrapper.active .diagonal-box:not(.active).bg-2 .diagonal-bg-text {
        transform: rotate(-45deg) translateY(-40px) translateX(-296px); }
      .diagonals .diagonal__wrapper.active .diagonal-box.active {
        height: 150%; }
        .diagonals .diagonal__wrapper.active .diagonal-box.active .diagonal-box__content {
          max-width: 600px; }
        .diagonals .diagonal__wrapper.active .diagonal-box.active .diagonal-box__description {
          display: block; }
    .diagonals .diagonal__wrapper.active .bg-2:not(.active) .diagonal-bg-text {
      top: 38%;
      left: -136%; }
    .diagonals .diagonal__wrapper.active .bg-2.active + .bg-3 .diagonal-bg-text {
      top: -266%;
      left: 194%; }
    .diagonals .diagonal__wrapper.active .bg-3:not(.active) .diagonal-bg-text {
      left: -277%;
      top: 134%; }
    .diagonals .diagonal__wrapper.active .bg-4:not(.active) .diagonal-bg-text {
      top: -89%;
      left: 46%; }
    .diagonals .diagonal__wrapper.active .bg.active .diagonal-bg-text {
      display: none; }
  .diagonals .btn--back {
    position: absolute;
    left: 35px;
    top: 35px; }
  .diagonals .diagonal-box {
    position: relative;
    padding: var(--skew-padding) 0;
    transform: skewy(var(--angle));
    transform-origin: 50% 0;
    outline: 1px solid transparent;
    backface-visibility: hidden;
    display: flex;
    justify-content: center;
    height: 30%; }
    .diagonals .diagonal-box .diagonal-bg-text {
      transform: rotate(-45deg);
      position: absolute;
      left: 0;
      top: -55px;
      right: 0;
      bottom: 0;
      font-size: 100px;
      font-family: 'Intro', sans-serif;
      font-weight: 700;
      text-transform: uppercase;
      -webkit-text-fill-color: transparent;
      -webkit-text-stroke-width: 2px;
      -webkit-text-stroke-color: black;
      -webkit-background-clip: text;
      background-clip: text;
      display: none;
      z-index: -1;
      text-indent: -80px; }
    .diagonals .diagonal-box:hover .diagonal-bg-text {
      display: block; }
  .diagonals .bg {
    background-image: linear-gradient(45deg, #654ea3, #eaafc8);
    color: #f1f1f1; }
    .diagonals .bg:hover {
      background-image: linear-gradient(45deg, #a30047, #eaafc8); }
    .diagonals .bg.active:hover {
      background-image: linear-gradient(45deg, #654ea3, #eaafc8); }
  .diagonals .bg-1 {
    background-image: linear-gradient(45deg, #654ea3, #eaafc8);
    color: #f1f1f1; }
    .diagonals .bg-1 .diagonal-box__content {
      left: 10%; }
    .diagonals .bg-1.active .diagonal-box__content {
      left: 17%; }
  .diagonals .bg-2 .diagonal-box__content {
    left: 31%; }
  .diagonals .bg-2.active .diagonal-box__content {
    left: 25%; }
  .diagonals .bg-3 .diagonal-box__content {
    left: 53%; }
  .diagonals .bg-3.active .diagonal-box__content {
    left: 30%; }
  .diagonals .bg-4 .diagonal-box__content {
    left: 75%; }
  .diagonals .bg-4.active .diagonal-box__content {
    left: 35%; }
  .diagonals .diagonal-box__description {
    display: none; }
  .diagonals .diagonal-box__content {
